<template>
  <div class="note_box">
    <div class="note_li" v-for="item in MyNote">
      <div class="note_lit">{{ item.noteContent }}</div>
      <div class="note_lit2">
        <div class="note_lit2l">
          <p>
            <i class="el-icon-date"></i
            >{{ item.createTime | filterDate("YYYY-MM-DD") }}
          </p>
        </div>
        <div class="note_lit2r">
          <p @click="updateNote(item)">
            <i class="el-icon-edit-outline"></i>修改
          </p>
          <p @click="removeNote(item.id)"><i class="el-icon-delete"></i>删除</p>
        </div>
      </div>
      <div class="note_lit3">原文：{{ item.originalText }}</div>
      <div class="note_lit4" @click="toread(item)">
        <el-image
          class="note_lit4_img"
          :src="item.cover"
          fit="contain"
        ></el-image>
        <p>{{ item.soureName }}</p>
      </div>
    </div>
    <noData v-if="total == 0"></noData>
    <el-pagination
      style="float: right"
      v-show="total > 0"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryParams.pageNum"
      :page-sizes="[10, 20, 30, 40, 50]"
      :page-size="queryParams.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
    <!-- <div class="note_li">
			<div class="note_lit">这是中国作家协会副主席 著名作家 叶辛 写的，非常值得阅读这是中国作家协会副主席 著名作家 叶辛 写的，非常值得阅读这是中国作家协会副主席 著名作家叶辛写的，非常值得阅读</div>
			<div class="note_lit2">
				<div class="note_lit2l">
					<p><i class="el-icon-date"></i>2021-12-21</p>
				</div>
				<div class="note_lit2r">
					<p><i class="el-icon-edit-outline"></i>修改</p>
					<p><i class="el-icon-delete"></i>删除</p>
				</div>
			</div>
			<div class="note_lit3">
				原文：这是一本关于山城重庆的书，一本及时的书，鲜活生动的书，让我们振奋的书。
			</div>
			<div class="note_lit4">
				<el-image class="note_lit4_img" :src="require('@/assets/images/luxun.png')"></el-image>
				<p>全能练考卷北师版二年级下册全能练考卷北师版二年级下册</p>
			</div>
		</div> -->
  </div>
</template>

<script>
import { listMyNote } from "@/api/user/mynote.js";
import { removeNoteById, update } from "@/api/special/special";
export default {
  data() {
    return {
      MyNote: [],
      queryParams: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0,
    };
  },
  watch: {
    $route(route) {
      this.getList();
    },
  },
  components: {
    noData: () => import("@/components/noData.vue"),
  },
  created() {
    this.getList();
  },
  methods: {
    handleClick() {},
    toDetail(id) {
      this.$router.push({
        path: "/special/specialDetail",
        query: {
          subjectId: id,
        },
      });
    },
    toread(item) {
      let url = this.$router.resolve({
        path: "/onlineReading",
        query: {
          goodsId: item.goodsId,
          resourceType: "goods",
          resource: "bookCity",
          relevanceId: item.goodsId,
        },
      });
      window.open(url.href, "_blank");
    },
    removeNote(id) {
      this.$confirm("确认删除吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        removeNoteById(id).then((response) => {
          this.getList();
          this.$message({
            type: "success",
            message: "删除成功",
          });
        });
      });
    },
    updateNote(item) {
      var note = JSON.parse(JSON.stringify(item));
      this.$prompt("请输入笔记内容", "提示", {
        inputValue: note.noteContent,
        inputType: "textarea",
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        closeOnClickModal: false,
        inputValidator: (value) => {
          // 点击按钮时，对文本框里面的值进行验证
          if (!value || /^\s+$/.test(value)) {
            return "请输入笔记内容";
          }
          if (value.length > 100) {
            return "不能超过100字";
          }
        },
      }).then(({ value }) => {
        value=value.trim()
        note.noteContent = value;
        //修改笔记内容
        update(note).then((response) => {
          this.$message({
            type: "success",
            message: "操作成功",
          });
          this.getList();
        });
      });
    },
    getStaffList() {
      this.StaffList = [];
      return getStaffList({
        pageNumber: 1,
        pageSize: 99999,
      }).then((res) => {
        res.data.list.forEach((item, index) => {
          this.StaffList.push({
            id: item.id,
            name: item.staffName,
          });
        });
      });
    },
    getList() {
      listMyNote(this.queryParams).then((res) => {
        this.MyNote = res.rows;
        this.total = res.total;
      });
    },
    //每页条数
    handleSizeChange(val) {
      this.queryParams.pageSize = val;
      this.getList();
    },
    //当前页面
    handleCurrentChange(val) {
      this.queryParams.pageNum = val;
      this.getList();
    },
    handdelete(row) {
      this.$confirm("确认删除吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        myResourceremove({
          id: row.id,
        }).then((res) => {
          this.$message({
            type: "success",
            message: "删除成功!",
          });
          this.getList();
        });
      });
      console.log(row);
    },
  },
};
</script>

<style>
.note_box {
  width: 100%;
  float: left;
  padding: 20px;
  box-sizing: border-box;
  background: #fffefd;
}
.note_li {
  width: 100%;
  float: left;
  border: 1px solid #f2f2f2;
  margin-bottom: 20px;
  padding: 10px;
  box-sizing: border-box;
}
.note_lit {
  font-size: 14px;
  width: 100%;
  float: left;
  line-height: 17px;
  margin-bottom: 10px;
  font-family: PingFang SC;
  font-weight: 400;
  color: #333333;
}
.note_lit2 {
  width: 100%;
  float: left;
}
.note_lit2l {
  float: left;
  font-size: 12px;
  font-family: PingFang SC;
  font-weight: 400;
  color: #999999;
  line-height: 17px;
}
.note_lit2l i {
  margin-right: 8px;
}
.note_lit2r {
  float: right;
}
.note_lit2r p {
  float: left;
  margin-left: 10px;
  font-size: 12px;
  font-family: PingFang SC;
  font-weight: 400;
  color: #999999;
  line-height: 17px;
  cursor: pointer;
}
.note_lit3 {
  width: 100%;
  float: left;
  font-size: 14px;
  font-family: PingFang SC;
  font-weight: 400;
  color: #666666;
  line-height: 19px;
  margin: 6px 0;
}
.note_lit4 {
  width: 100%;
  float: left;
  height: 60px;
  background: #f2f2f2;
  cursor: pointer;
}
.note_lit4_img {
  width: 50px;
  height: 50px;
  float: left;
  margin-left: 7px;
  margin-top: 5px;
}
.note_lit4 > p {
  float: right;
  width: calc(100% - 70px);
  line-height: 50px;
  margin-top: 5px;
}
</style>